.parallax {
    width: 100vw;
    background-image: url(../img/parallax.jpg);

    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.parallax .container, .parallax .row {
    height: 100%;
    min-height: 500px;
}
.parallax .col-md-4 {
    padding-right: 2px;
    padding-left: 2px;
}
/* cover */
.parallax-cover {
    width: 100%;
    min-height: 500px;
    background-color: #00000088;
    padding-bottom: 8px;
}
/* parallax item */
.parallax-item {
    /* box-sizing: border-box; */
    border: 1px solid white;
    text-align: center;
    padding: 25px 0px;
    background-color: rgba(131, 131, 131, 0.712);
}
.parallax-item i {
    font-size: 45px;
    margin-bottom: 30px;
}
.parallax-item h5 {
    font-weight: 900;
    font-size: 33px;
    margin-bottom: 30px;
}
.parallax-item p  {
    color: rgb(219, 219, 219);
}
