@import './Navbar.css';
@import "./Carousel.css";
@import "./Principal.css";
@import './Footer.css';
@import './parallax.css';
@import './owl.css';

/* Configurações gerais */
body {
    margin: 0px;
    padding: 0px;
    overflow-x: hidden;
    color: white;
    background-color: rgb(255, 255, 255);
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

/* link config */
a {
    border-bottom: 2px solid transparent;
    color: black;
}

a:hover {
    color: rgb(95, 95, 95);
    text-decoration: none;
}


/* --------------------------------------- */
/* classes */
/* --------------------------------------- */

.title  {
    background-image: linear-gradient(90deg, #0ecdf0, #00bbfc, #00a6ff, #008dff, #006fff, #005dfd, #1e49f8, #362ef1, #452be7, #5028dd, #5725d3, #5d23c9);
    background-clip: text;
    -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* margin & padding */
.pt-150 {
    padding-top: 150px !important;
}

.pb-150 {
    padding-bottom: 150px !important;
}

.mt-150 {
    margin-top: 150px;
}

.padding-x-75 {
    padding: 75px 0px;
}

.mt-75 {
    margin-top: 75px;
}


/* Buttons */
.btn-gradient {
    border: 1px solid rgba(255, 255, 255, 0.452);
    padding: 7px 25px;
    color: white !important;
    background-image: linear-gradient(to right bottom, #0ecdf0, #00bbfc, #00a6ff, #008dff, #006fff, #005dfd, #1e49f8, #362ef1, #452be7, #5028dd, #5725d3, #5d23c9);
}

.btn-gradient:hover {
    background-image: linear-gradient(to right bottom, rgba(14, 205, 240, 1) 0%, rgba(93, 35, 201, 1) 100%);
}


/* Background */
.lemes-bg,
.lemes-bg-alt {
    color: black;
    padding: 75px 0px;
}

.gray-bg {
    background-color: #f1f1f1;
}

