/* navbar */
/* muda a cor da navbar quando ela não está no topo */
.nav-after-scroll {
    background: #0b5be4;
    transition: 1000ms ease;

}

/* Links */
/* redes sociais */
nav i {
    font-size: 40px;
}

/*  redes sociais dropmenu config */
.dropdown-item span {
    position: relative;
    top: -10px;
    left: +3px;
}

/* estilo dos links da navbar */
nav ul a {
    color: white;
}

nav ul a:hover {
    border-bottom: 2px solid white;
    color: white;
    border-radius: .0rem;
}

/* retira as bordas dos links na versao mobile */
@media only screen and (max-width: 992px) {
    nav ul a:hover {
        border: none;
    }
}

/* coloca margin top nos links quando os links estão retraídos */
@media only screen and (max-width: 991px) {
    .navbar-nav {
        margin-top: 10px;
    }
}

/* configuração do dropdown menu e links*/
.dropdown-menu {
    background: #0b5be400;
    border: 1px solid #ffffff;
}

.nav-after-scroll .dropdown-menu {
    background: #0b5be4;
    border: 1px solid #ffffff;
    transition: 1000ms ease;

}

/* dropmenu item style */
nav .dropdown-item {
    color: white !important;
    border: 2px solid transparent;
    background: transparent !important;
    border-radius: 0 !important;
}

.navbar-brand img {
    max-height: 70px;
}