/* MAIN */

/* Modal */
.modal-content {
    background: rgb(255, 255, 255);
    background: linear-gradient(180deg, rgba(255, 255, 255, 1) 32%, rgba(218, 218, 218, 1) 100%);
}
/* redes sociais links */
.modal-content a {
    font-size: 42px;
    padding: 0px 8px;
    color: black;
}
.modal-content a:hover {
    color: rgb(49, 49, 49);
}
.modal-content .link a {
    font-size: 1em !important;
}
/* alinha os headers */
.my-modal-header {
    /* text-align: initial; */
    padding: 5px 0px 0px 15px;
}
/* separa os títulos com borda branca */
.my-modal-content {
    padding: 5px 0px;
    border-bottom: 1px solid black;
}
/* alinha o link do modal à direita */
.my-modal-footer {
    display: flex;
    padding: 1rem;
    justify-content: flex-end;
}



/* Cursos */
.card {
    background: rgb(255, 255, 255);
    background: linear-gradient(180deg, rgba(255, 255, 255, 1) 32%, rgba(218, 218, 218, 1) 100%);
    border: 1px solid rgb(0, 0, 0);
    margin-right: 3px;
    margin-top: 3px;
    /* padding: 1px; */
}

