/* Texto que fica em cima do carousel */

/* para as setas do carousel ficarem acima do texto */
.carousel-control-prev-icon .carousel-control-next-icon {
    z-index: 1000;
}
/* Container do texto, escurece a imagem */
.carousel-txt {
    position: absolute;
    top: 0px;
    z-index: 1;
    height: calc(100vh - 0px);
    /* min-height: 652px; */
    width: 100vw;
    background-color: #0b5be4bd;
}
/* texto do carousel */
.carousel-txt-txt {
    color: white;
    text-shadow: 2px 2px 0px #000000;
    font-size: 30px;
    text-align: center;
    word-wrap: break-word;
    position: relative;
    width: 80%;
    top: 45%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    /* border: 1px solid red; */
}
/* título */
.carousel-txt-txt h1,
h1 {
    font-size: 56px;
    font-weight: bold;
}
/* dimiuí a fontsize para versão mobile */
@media only screen and (max-width: 986px) {
    .carousel-txt-txt {
        /* top: calc(50% + 68px); */
        top: 50%;
        font-size: 20px;
    }
    .carousel-txt-txt h1, h1 {
        font-size: 40px;
    }
}
/* aumenta a width do texto em celulares */
@media only screen and (max-width: 666px) {
    .carousel-txt-txt {
        width: 100%;
    }
}
.carousel-slogan {
    text-align: center;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}
.carousel-slogan img {
    max-width: 100%;
    padding-left: 15%;
    padding-right: 15%;
}